.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App {
  text-align: center;
  background-color: #f0f8ff; /* Màu nền nhạt */
  min-height: 100vh; /* Chiều cao tối thiểu của màn hình */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-header {
  background-color: #282c34; /* Màu nền cho header */
  min-height: 40vh; /* Chiều cao của header */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white; /* Màu chữ trắng */
}

button {
  background-color: #61dafb; /* Màu nền cho nút */
  color: white; /* Màu chữ trên nút */
  border: none;
  padding: 10px 20px;
  border-radius: 5px; /* Bo góc cho nút */
  cursor: pointer;
  transition: background-color 0.3s; /* Hiệu ứng chuyển màu */
}

button:hover {
  background-color: #21a1f1; /* Màu nền cho nút khi hover */
}

button {
  transition: transform 0.2s; /* Hiệu ứng chuyển động */
}

button:active {
  transform: scale(0.95); /* Thu nhỏ nút khi nhấn */
}
